    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
a.back{
  position: fixed;
  z-index: 55;
  width: 50px;
  height: 50px;
  bottom: 100px;
  right: 30px;
  background-color: #797979;
  display: block;
  cursor: pointer;
}
a.back:hover{
  background-color: #FC7878;
}
a.back i{
  width: 25px;
  height: 22px;
  float: left;
  margin-left: 15px;
  margin-top: 15px;
  background-image: url(data:image/png;base64,);
  background-position: -28px -25px;
  background-repeat: no-repeat;
}

.head {
  height: 108px;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.head .logo {
  width: 270px;
  height: 48px;
  margin-right: 120px;
}
.head .nav-head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.head .nav-head a {
  color: #333;
  font-size: 16px;
  padding: 0 10px;
}
.head .nav-head a:hover {
  color: #fc8080;
}


.swiper-container {
    width: 100%;
    height: 425px;
}
.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.swiper-slide-active{
  z-index: 10;
}
.swiper-button-prev,
  .swiper-button-next {
    position: absolute;
    top: 50%;
    width: 27px;
    height: 44px;
    margin-top: -22px;
    z-index: 10;
    cursor: pointer;
    background-size: 27px 44px;
    background-position: center;
    background-repeat: no-repeat;
}
.swiper-button-prev,.swiper-button-next {
    background-color: rgba(0,0,0,.1);
}
.swiper-button-prev:hover,.swiper-button-next:hover{
    background-color: rgba(0,0,0,.6);
}

